import { mapGetters } from "vuex";
import { mapState } from "vuex";
import foot from "../foot.vue";
import menu from "../common/menu.vue";
import guestCenter from "../common/menu/guestCenter.vue";
export default {
  data() {
    return {
      menuFlag: false,
      centerFlag: false,
      shortcutList: [], //
      serviceList: {},
      localshortcutList:[],
    };
  },
  created: function() {
    //
    localStorage.footFlag = 4;
    localStorage.HOTELID = 3;

    this.localshortcutList = [
      {
        key: "orderFood",
        title: "Order",
        pic: require("../../assets/images/order.png"),
        linkTo: "/orderFood"
      },
      {
        key: "shopping",
        title: "Shopping",
        pic: require("../../assets/images/shopping1.png"),
        linkTo: "/shopping"
      },
      {
        key: "maintain",
        title: "Maintenance",
        pic: require("../../assets/images/plumbing.png"),
        linkTo: "/maintain"
      },
      {
        key: "clean",
        title: "Housekeeping",
        pic: require("../../assets/images/clean.jpeg"),
        linkTo: "/cleanDetail"
      },
      {
        key: "rating",
        title: "Rating",
        pic: require("../../assets/images/rate.png"),
        linkTo: "/rating"
      },
      {
        key: "otherService",
        title: "Other",
        pic: require("../../assets/images/other.png"),
        linkTo: "/interactiveOther"
      }
    ];

    //
    let params = {
      hotelid: localStorage.HOTELID,
      // lang: localStorage.LANGUAGE
    }; 
    this.$store.dispatch("getServiceList", params).then(res => {
      let dataList = res.data; 
      if (dataList.code == 0) {
        //    
        this.serviceList = dataList.data.list; 
        console.log(this.serviceList.length);
        //
        for (var item in this.serviceList) {
          for (var i in this.localshortcutList) {
            if (
              this.serviceList[item].title_lang2 ==
              this.localshortcutList[i].title
            ) { 
              this.serviceList[item].linkTo = this.localshortcutList[i].linkTo;
              this.shortcutList.unshift(this.serviceList[item]);
            }
          }
        }
      } else {
        this.$dialog.toast({ mes: res.msg, timeout: 1000 });
      }
    });
  },
  mounted: function() {
    //falg
    isHomePage(1);
  },
  components: {
    "v-foot": foot,
    "v-menu": menu,
    "v-guestCenter": guestCenter
  },
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  }
};



