import { mapGetters } from "vuex";
import { mapState } from "vuex";
// import chartfoot from "../chartFoot.vue";
export default {
  data() {
    return {
      list: [
        {
          label: "",
          icon: "demo-icons-category1",
          color: "gray",
          data: [
            { id: "1", title: "", price: "3",img: require("../../assets/images/rice.jpeg") },
            { id: "2", title: "", price: "4",img: require("../../assets/images/noodle.jpg") }
          ]
        },
        {
          label: "",
          icon: "demo-icons-category2",
          color: "blue",
          data: [
            { id: "3", title: "", price: "8",img: require("../../assets/images/onionring.jpeg") },
            { id: "4", title: "", price: "5",img: require("../../assets/images/chip.jpeg") },
            { id: "5", title: "", price: "10",img: require("../../assets/images/wing.jpg") }
          ]
        },
        { label: "", icon: "demo-icons-category3", color: "yellow" },
        { label: "", icon: "demo-icons-category4", color: "green" },
        { label: "", icon: "demo-icons-category5", color: "saddlebrown" },
        {
          label: "",
          icon: "demo-icons-category6",
          color: "darkgoldenrod"
        }
      ],
      selectedNum: 0,
      itemQuantity: 1,
      totalPrice: 0,
      cart: []
    };
  },
  created: function() {
    if (localStorage.Quantity) {
      this.cart = JSON.parse(localStorage.getItem("cart"));
      this.selectedNum = parseInt(localStorage.Quantity);
      this.totalPrice = parseInt(localStorage.totalPrice);
    }
  },
  mounted: function() {},
  methods: {
    change: function() {},

    addToCart: function(data) {
      let _this = this;
      _this.selectedNum++;
      _this.totalPrice += parseInt(data.price);
      var existIndex = _this.cart.findIndex(function(item, index) {
        return item.id === data.id;
      });
      if (existIndex == -1) {
        var cartIndex = _this.cart.length;
        _this.cart.push(data);
        _this.$set(_this.cart[cartIndex], "quantity", 1);
        _this.saveCart();
        return;
      } else {
        _this.cart[existIndex].quantity += 1;
        _this.saveCart();
        return;
      }
    },
    checkOut: function() {
      this.$router.push({ path: "/shoppingCart" });
    },
    saveCart: function() {
      localStorage.setItem("cart", JSON.stringify(this.cart));
      localStorage.setItem("Quantity", parseInt(this.selectedNum));
      localStorage.setItem("totalPrice", parseInt(this.totalPrice));
    }
  },
  components: {
    // "v-foot": chartfoot
  },
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  }
};



