import { mapState } from 'vuex'
export default {
    data() {
        return {};
    },
    created: function () {
        let params = {
            id:1,
            userid:1,
            value:'test',
            ip:'127.0.0.1',
            hotelid:1
        }
        this.$store.dispatch('commitJudge', params)
    },
    methods: {},
    mounted() {
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    }
}



