
import { mapState } from "vuex";
import { mapGetters } from "vuex";
import foot from "../foot.vue";
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { getDistance } from "../../config/mUtils";
export default {
data() {
return {
isItemShow: false,
typeList: [],
dataList: [],
nextPage: 0,
distanceFlag: false,
typeId: 0,
noData: false
};
},
created: function() {
localStorage.footFlag = 3;
},
mounted: function() {
let _this = this;

let params = {
hotelid: localStorage.HOTELID,
lang: localStorage.LANGUAGE
};
this.$store.dispatch("getPoiTypeList", params).then(function(res) {
_this.typeList = res.data.data.list;
_this.typeId = _this.typeList[0].id;
$(document).ready(function() {
new Swiper(".raiders .swiper-container", {
pagination: ".swiper-pagination",
slidesPerView: 3,
paginationClickable: true,
spaceBetween: 0
});

$(".ra").on("click", ".swiper-slide", function() {
$(this)
.addClass("active")
.siblings()
.removeClass("active");
});


if (_this.nextPage != "-1") {
var flag = "inifinte";
var id = _this.typeId;
_this.getList(id, flag);
} else {
_this.noData = true;
}
});
});

isHomePage(1);
},
methods: {
getList: function(id, flag) {
let _this = this;
_this.typeId = id;
if (flag == "click") {
this.dataList = [];
_this.nextPage = 0;
_this.noData = false;
}
setTimeout(function() {
let params = {
hotelid: 3,
typeid: id,
lang: localStorage.LANGUAGE,
limit: 5,
page: _this.nextPage
};
_this.$store.dispatch("getPoiList", params).then(res => {
let list = res.data.data.list;
_this.nextPage = res.data.data.nextPage;

var dataArr = [];
for (var item in list) {
let distance = parseFloat(
getDistance(
localStorage.lng,
localStorage.lat,
list[item].lat,
list[item].lng
)
).toFixed(2);
list[item].distance = distance;
dataArr.push(list[item]);
}
_this.dataList = _this.dataList.concat(dataArr);
});
}, 300);
},













changeTab: function(id) {
var flag = "click";
this.getList(id, flag);
},
toDetail: function(id) {
let data = {};
for (var key in this.dataList) {
if (this.dataList[key].id == id) {
data = this.dataList[key];
break;
}
}
this.$router.push({ path: "/raidersDetail", query: { data: data } });
}
},
components: {
"v-foot": foot,
PopupPicker
},
computed: {
...mapState({
language: state => state.language.language
})
}
};



