import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      dataList: [],
      nextPage: 1,
      noData: false,
      pageFlag: "",
      selectedNum: 0,
      itemQuantity: 1,
      totalPrice: 0,
      cart: []
    };
  },
  created: function() {
    this.pageFlag = this.$route.query.pageFlag;
    if (localStorage.Quantity) {
      this.cart = JSON.parse(localStorage.getItem("cart"));
      this.selectedNum = parseInt(localStorage.Quantity);
      this.totalPrice = parseInt(localStorage.totalPrice);
    }
  },
  filters: {
    formatMoney: function(value) {
      return " " + value.toFixed(2);
    }
  },
  methods: {
    apply: function() {
      this.$dialog.confirm({
        title: "",
        mes: "",
        opts: () => {
          alert(JSON.stringify(this.cart));

          //   let params = [];
          //   for (var index = 0; index < this.cart.length; index++) {
          //     params[i] = {
          //       hotelid: localStorage.HOTELID,
          //       shoppingid: this.cart[i].id,
          //       token: localStorage.TOKEN,
          //       count: this.cart[i].quantity
          //     };
          //   }
          //   this.$store.dispatch("getShoppingOrder", params).then(res => {
          //     if (res.data.code == 0) {
          //       this.$dialog.toast({
          //         mes: _this.language.msg.buy_info,
          //         timeout: 1000
          //       });
          //       localStorage.c;
          //     } else {
          //       this.$dialog.toast({ mes: res.data.msg, timeout: 1000 });
          //     }
          //   });
          localStorage.Quantity = 0;
          localStorage.totalPrice = 0;
          localStorage.cart = [];
        }
      });
    },

    checkOut: function() {
      // this.$router.push({path:'/shoppingCart'});
    },

    goBack: function() {
      this.$router.go(-1);
    },
    changeMoney: function(item, operation) {
      if (operation < 0) {
        if (item.quantity == 1) {
          return;
        }
        item.quantity--;
        this.totalPrice -= parseInt(item.price);
        this.selectedNum--;
      } else {
        item.quantity++;
        this.totalPrice += parseInt(item.price);
        this.selectedNum++;
      }
    },
    removeConfirm: function(data) {
      this.$dialog.confirm({
        title: "",
        mes: "",
        opts: () => {
          var index = this.cart.indexOf(data);
          if (index > -1) {
            this.cart.splice(index, 1);
            this.selectedNum -= data.quantity;
            this.totalPrice -= data.price * data.quantity;
            localStorage.setItem("cart", JSON.stringify(this.cart));
            localStorage.setItem("Quantity", parseInt(this.selectedNum));
            localStorage.setItem("totalPrice", parseInt(this.totalPrice));
          }
        }
      });
    }
  },
  mounted: function() {},
  components: {},
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  }
};



