import { mapState } from 'vuex'
export default {
    data() {
        return {
            judgeList:[]
        };
    },
    created: function () {
        let _this = this
        let params = {
            id:1,
            limit:10,
            page:1
        }
        this.$store.dispatch('JudgeList', params).then(function (res) {
            _this.judgeList =  res.data.data.list
            console.log(res.data.data.list)
        })
    },
    methods: {},
    mounted() {
    },
    computed: {
        ...mapState({
            language: state => state.language.language
        })
    }
}



