import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      page1: 1,
      categoryId: 0,
      page2: 1,
      pageSize: 10,
      dataList: [],
      list: [
        {
          pic: require("../../assets/images/airconditioning.png"),
          title: "",
          type: 0
        },
        {
          pic: require("../../assets/images/plumbing.png"),
          title: "",
          type: 1
        },
        {
          pic: require("../../assets/images/electrical.png"),
          title: "",
          type: 2
        },
        {
          pic: require("../../assets/images/furniture.png"),
          title: "",
          type: 3
        },
        {
          pic: require("../../assets/images/audio.png"),
          title: "",
          type: 4
        },
        {
          pic: require("../../assets/images/internet.png"),
          title: "",
          type: 5
        }
      ]
    };
  },
  created: function() {
    let params = {
      hotelid: localStorage.HOTELID,
      categoryId: this.$route.query.categoryId,
      status: 1
    };
    this.$store.dispatch("getTaskList", params).then(res => {
      let dataList = res.data;
      if (dataList.code == 0) {
        this.dataList = dataList.data.list;
        // alert(JSON.stringify(dataList.data));
      } else {
        this.$dialog.toast({ mes: res.msg, timeout: 1000 });
      }
    });
  },
  methods: {
    goNext: function() {
      alert(this.title);
    }
  },
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  }
};



