    import { mapState } from 'vuex'
	export default {
		data() {
			return {
				telList:[],
                stateFlag:''
			};
		},
        created:function () {
            this.stateFlag = localStorage.stateFlag
		    if (localStorage.TOKEN){
		    	let _this = this;
                let params = {'token':localStorage.TOKEN,'hotelid':localStorage.HOTELID,lang:localStorage.LANGUAGE};
                this.$store.dispatch('getHotelTelList', params).then(function (res) {
	                _this.telList = res.data.data.list;
                });

            }else{
		    	this.telList =[
                    {
                    	title:this.language.func.service,
                        telList: [
	                        {title:this.language.func.contact,tel:'8610-6551-8686'},
	                        {title:this.language.func.extension,tel:'8001'},
	                        {title:this.language.func.extension,tel:'8002'},
                            {title:this.language.func.extension,tel:'8003'}
                        ]
                    }
				    // {
					//     title:'Facilities',
					//     telList: [
					// 	    {title:'RESTAURANT',tel:'1004'}
					//     ]
				    // }
                ]
            }
        },
		methods: {
		    call:function (phone) {
//		        alert(phone)
                window.location.href = "tel:"+phone;
            }
		},
        computed: {
            ...mapState({
                language: state => state.language.language,
                hotelTelList: state => state.translate.hotelTelList
            })
        },
        mounted:function () {
            //falg
            isHomePage(0)
        }

	};



