	import { mapGetters } from 'vuex'
    import { mapState } from 'vuex'
	export default {
		data() {
			return {
                tabList:[],//
                ssrList:[],
                show: false,
                dataList:{}
			}
		},
        created:function () {

            let _this = this
			this.ssrFlag = this.$route.query.ssrFlag

            // ssr 
            let params = {
                hotelid: localStorage.HOTELID,
                lang:localStorage.LANGUAGE
            }
            this.$store.dispatch('getRss', params).then(function (res) {
                _this.ssrList = res.data.list
                // 
                var activeList = localStorage.activeList
                if(activeList){
                    // 
                    for (var item in activeList) {
                        for (var i in _this.ssrList) {
                            if (activeList[item] == _this.ssrList[i].id) {
                                _this.ssrList[i].active = 1
                            }
                        }
                    }
                } else {
                    for (var i in _this.ssrList) {
                        _this.ssrList[i].active = 1
                    }
                }

                //swiper
                $(document).ready(function () {
                    var swiper = new Swiper('.ssr .swiper-container', {
                        pagination: '.swiper-pagination',
                        slidesPerView: 2.5,
                        paginationClickable: true,
                        spaceBetween: 0
                    });
                    // 
                    $('.ssrList').on('click','li',function () {
                        if ($(this).hasClass('active')) {
                            $(this).removeClass('active')
                        } else {
                            $(this).addClass('active')
                        }

                    })
                    $('.tab-change .swiper-slide').click(function () {
                        $(this).addClass('active').siblings().removeClass('active')
                    })
                })

                //
                _this.changeTab(_this.ssrList[0].ssr,_this.ssrList[0].name)

            })

            console.log('fg',this.ssrList)
        },
		methods: {
			getTab:function (arr) {
				this.tabList=[];
                for(var index in arr){
                	this.tabList.push(arr[index])
                }
			},
            changeTab:function (url,name) {
			    this.$store.dispatch('showLoading')
                var ssrBase = 'https://bird.ioliu.cn/v1/?url='
				let _this = this;
			    var urlPath;
			    if(name == '' || name == 'news') {
                    urlPath = 'http://lixin.xapi.wangtiansoft.com/newsgn/rss_newsgn.xml'
                } else if(name == '' || name == 'caijing') {
                    urlPath = 'http://lixin.xapi.wangtiansoft.com/scroll/rss_scroll.xml'
                } else if(name == '' || name == 'yule') {
                    urlPath = 'http://lixin.xapi.wangtiansoft.com/rss/yule.xml'
                }else {
                    urlPath = ssrBase + url
                }
				//jqget
	            $.get(urlPath,function (res) {
		            var result = [];
		            var resXml = $(res);
		            resXml.find('item').each(function () {
			            var title = $(this).find('title').text();
			            var link = $(this).find('link').text();
			            var author = $(this).find('author').text();
			            var pubDate = $(this).find('pubDate').text();
			            var description = $(this).find('description').text()+'...';
			            var obj = { title: title, link:link, author:author, pubDate:pubDate, description:description}
			            result.push(obj)
		            })
		            _this.dataList = result;
		            console.log(result)
                    _this.$store.dispatch('hideLoading')
	            })

            },
			openDetail:function (url,title) {
                if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
                    openFile(url)
                } else {
                    openUrl(url,title)
                }

			},
            setActive:function () {
			    let _this = this;
			    var setArr = []
                $('.ssrList li').each(function () {
                    if ($(this).hasClass('active')) {
                        setArr.push($(this).find('input').val())
                    }
                })
                this.show = !this.show
                localStorage.activeList = setArr

                // ssr 
                let params = {
                    hotelid: localStorage.HOTELID,
                    lang:localStorage.LANGUAGE
                }
                this.$store.dispatch('getRss', params).then(function (res) {
                    _this.ssrList = res.data.list

                    // 
                    var activeList = localStorage.activeList
                    // 
                    for (var item in activeList) {
                        for (var i in _this.ssrList) {
                            if (activeList[item] == _this.ssrList[i].id) {
                                _this.ssrList[i].active = 1
                            }
                        }
                    }
                    console.log('bg',_this.ssrList)
                    //swiper
                    $(document).ready(function () {
                        var swiper = new Swiper('.ssr .swiper-container', {
                            pagination: '.swiper-pagination',
                            slidesPerView: 2.5,
                            paginationClickable: true,
                            spaceBetween: 0
                        });
                    })

                })
            }
		},
		mounted() {
            //falg
            isHomePage(0)
		},
        components:{
        },
        computed: {
            ...mapState({
                language: state => state.language.language
            })
        },
	};



