import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      content: ""
    };
  },
  created: function() {  
    let _this = this;
    this.$store.dispatch("showLoading");
    let params = {
      paramtype: "get",
      id: 2
    };
    this.$store.dispatch("groupDetail", params).then(function(res) {
      var ssrBase = "https://bird.ioliu.cn/v1/?url=";
      if (res.data.about) {
        $.get(ssrBase + res.data.about, function(res) {
          _this.content = res;
          _this.$store.dispatch("hideLoading");
        });
      }
    });
  },
  methods: {
    //
    goBack: function() {
      let flag = localStorage.menuFlag;
      switch (flag) {
        //
        case "home":
          this.$router.push("/home");
          break;
        //
        case "unlogin":
          this.$router.push("/unlogin");
          break;
        //
        case "guide":
          this.$router.push("/guide");
          break;
        //
        case "s_home":
          this.$router.push("/s_home");
          break;
      }
    }
  },
  mounted: function() {
    //falg
    isHomePage(0);
  },
  components: {}
};



